const QQMapWX = require('@/common/js/qqmap-wx-jssdk.min.js');
export default {
	data() {
		return {
			address: "正在获取地址...",
			longitude: 120.1887611219618,
			latitude: 30.19322157118055,
			current_long: 120.1887611219618,
			current_lat: 30.19322157118055,
			key: 'WL7BZ-TBAW3-7CB3K-YFHND-YTAJ3-5OF4F',
			mapCtx: null,
			location: false,
			qqmapsdk: null,
			mapObj: null
		}
	},
	onLoad() {
		this.qqmapsdk = new QQMapWX({
			key: this.key
		});
		this.currentLocation()
	},
	onReady() {





















	},
	methods: {
		// 地图发生变化的时候，获取中间点，也就是cover-image指定的位置
		regionchange(e) {
			console.log(e,'1');
			if (e.type == 'end' && (e.causedBy == 'scale' || e.causedBy == 'drag')) {
				this.address = "正在获取地址...";
				if (!this.mapCtx) {
					this.mapCtx = uni.createMapContext("maps");
				}
				this.mapCtx.getCenterLocation({
					type: 'gcj02',
					success: (res) => {
						console.log(res)
						this.latitude = res.latitude;
						this.longitude = res.longitude;
						this.getAddress(res.longitude, res.latitude);
					}
				})
			}
		},
		//根据经纬度获取地址信息
		getAddress: function(lng, lat) {
			this.qqmapsdk.reverseGeocoder({
				location: {
					latitude: lat,
					longitude: lng
				},
				success: (res) => {
					console.log(res)
					this.address = res.result.formatted_addresses.recommend
				},
				fail: (res) => {
					this.address = "获取位置信息失败"
				}
			})
		},
		//当前位置
		currentLocation() {
			const that = this;
			uni.getLocation({
				type: 'gcj02',
				success(res) {
					console.log(res);
					that.latitude = res.latitude;
					that.longitude = res.longitude;
				}
			})
		},
		//将地图中心移动到当前定位点
		fnMoveToLocation (e) {
			
		}
	}
}
